@import url("https://fonts.googleapis.com/css?family=Prompt&display=swap");
@import url("https://fonts.googleapis.com/css?family=VT323&display=swap");

.Typist .Cursor {
  display: inline-block;
}
.Typist .Cursor--blinking {
  opacity: 1;
  animation: blink 1s linear infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  position: relative;
  width: 100%;
}
html {
  /*background: #031e11;
	*/
  font-size: 14px;
  cursor: url(./cur.png), auto;
  text-shadow: 2px 0px 0px rgba(36, 212, 50, 0.3),
    -2px 0px 0px rgba(46, 222, 60, 0.2);
}
body,
html,
#root {
  height: 100%;
  line-height: 1.42857;
  font-feature-settings: "tnum", "tnum";
  font-variant: tabular-nums;
}
.hiddenInput {
  position: fixed;
  top: -10%;
}
::selection {
  background-color: transparent;
}
.hiddenText {
  color: transparent;
  text-shadow: none;
}
.option {
  cursor: pointer;
}
@keyframes refreshFix {
  0% {
    background-position: center calc(25vh);
  }
  100% {
    background-position: center calc(-75vh);
  }
}
@keyframes refreshRate {
  0% {
    top: -3%;
  }
  100% {
    top: 103%;
  }
}
@keyframes scanline {
  0% {
    opacity: 0.2;
  }
  10% {
    opacity: 0.7;
  }
  15% {
    opacity: 0.9;
  }
  20% {
    opacity: 0.5;
  }
  25% {
    opacity: 0.2;
  }
  30% {
    opacity: 0.8;
  }
  35% {
    opacity: 1;
  }
  40% {
    opacity: 0.6;
  }
  42% {
    opacity: 0.1;
  }
  44% {
    opacity: 0.8;
  }
  46% {
    opacity: 0.2;
  }
  48% {
    opacity: 1;
  }
  50% {
    opacity: 0.1;
  }
  55% {
    opacity: 0.9;
  }
  60% {
    opacity: 0.7;
  }
  65% {
    opacity: 0.9;
  }
  70% {
    opacity: 0.6;
  }
  75% {
    opacity: 0.3;
  }
  75% {
    opacity: 0.7;
  }
  80% {
    opacity: 0.9;
  }
  85% {
    opacity: 0.5;
  }
  90% {
    opacity: 0.9;
  }
  100% {
    opacity: 0.2;
  }
}
@keyframes crt-glow {
  0% {
    opacity: 0.01;
  }
  100% {
    opacity: 0.03;
  }
}
.Typist {
  display: inline;
}
.Typist .Cursor {
  display: inline-block;
}
.Typist .Cursor--blinking,
.cursorBlink {
  opacity: 1;
  animation: blink 1s linear infinite;
}
@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
*::-webkit-scrollbar {
  width: 0.5em;
}
*::-webkit-scrollbar-thumb {
  background-color: #2ede3c;
  outline: none;
}
.blinking {
  animation: blink 60ms infinite;
}
u {
  cursor: pointer;
}
a {
  text-decoration: none;
  color: inherit;
  font-weight: bold;
}
.App {
  display: flex;
  flex-direction: column;
}
.border-up {
  border-top: 3px dashed;
}
.d-flex {
  display: flex;
}
@media (max-width: 768px) {
  .d-flex {
    flex-flow: column;
  }
}
@media (min-width: 769px) {
  .d-flex > * {
    flex: 1 1 0;
  }
}
.screen {
  padding: 0;
}
.screen .logo {
  max-width: 40px;
  max-height: 40px;
  vertical-align: middle;
}
.screen .portfolio .imgHeader {
  padding: 10px 15px;
  text-align: center;
}
.screen .portfolio .imgHeader > img {
  max-width: 100%;
  max-height: 250px;
  filter: sepia(1) hue-rotate(75deg) saturate(2) contrast(1.3);
}
.screen .portfolio .subtitle {
  text-decoration: underline;
}
.screen .portfolio .description {
  padding: 0px 10px;
}
.screen .options {
  display: flex;
  flex-flow: column;
  justify-content: center;
  border-right: 3px dashed;
}
@media (max-width: 768px) {
  .screen .options {
    border-right: none;
    border-bottom: 3px dashed;
  }
}
.screen .meHolder {
  text-align: center;
}
@media (min-width: 768px) {
  .screen .meHolder {
    position: absolute;
    top: 15px;
    right: 15px;
  }
}
.screen .meHolder .myPhoto {
  height: 150px;
  width: 150px;
}
.screen .kats {
  background-image: url(./parrots.jpeg);
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.screen .rdd {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: #f00;
  animation: fadeIn 10s linear both;
}
.screen .blkk {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  background-color: #000;
  animation: blink 2s infinite;
}
.screen .scanlinesV {
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0) 50%,
    rgba(0, 0, 0, 0.2) 70%,
    rgba(0, 0, 0, 0.6)
  );
  background-size: 0.3rem 100%;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  opacity: 0.1;
}
.screen .scanlines {
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0) 50%,
    rgba(0, 0, 0, 0.2) 70%,
    rgba(0, 0, 0, 0.6)
  );
  background-size: 100% 0.3rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  opacity: 0.8;
}
.screen .content {
  top: 0;
  left: 0;
  position: fixed;
  height: 100%;
  width: calc(100% - 2em);
  font-family: "VT323", monospace;
  padding: 0 1em;
  color: #2ede3c;
  font-size: 1.5em;
  display: flex;
  flex-direction: column;
}
.screen .content .contentBody,
.screen .content .portfolio {
  flex-grow: 1;
  overflow-y: auto;
  position: relative;
}
.screen .content > *,
.screen .content > .Typist * {
  margin: 0;
  word-break: break-word;
}
.screen .content .userChoice {
  font-style: italic;
  font-weight: bold;
  text-decoration: underline;
  color: #00ff16;
}
.screen .content .title {
  font-size: 1.7em;
}
@media (max-width: 768px) {
  .screen .content .title {
    font-size: 1.3em;
  }
}
.screen .chaosBG {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  filter: blur(2px) brightness(0.5);
  background-image: url(./asd.png);
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
}
.screen .scanline {
  position: fixed;
  top: -3%;
  left: -10%;
  width: 120%;
  height: 0.15rem;
  z-index: 2;
  background-color: #00ff43;
  filter: blur(15px);
  mix-blend-mode: color-burn;
  animation: scanline 66.66ms infinite, refreshRate 66.66s infinite;
  pointer-events: none;
}
.screen .sun {
  filter: blur(2px) brightness(0.9);
  opacity: 0.4;
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  background-image: url(./sun.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
}
@media (max-width: 768px) {
  .screen .sun {
    background-size: contain;
  }
}
.screen .sunLine {
  animation: refreshRate 66.66s infinite;
  filter: blur(5px) brightness(0.66);
  position: fixed;
  top: -3%;
  left: 0%;
  width: 100%;
  height: 33.33px;
  background-image: url(./sun.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: 720px;
}
@media (max-width: 768px) {
  .screen .sunLine {
    background-size: contain;
    background-attachment: scroll;
    background-image: none;
  }
  .screen .sunLine:before {
    animation: refreshFix 66.66s infinite;
    content: "";
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -10;
    background-image: url(./sun.svg);
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.screen .bg {
  animation: refreshRate 60s infinite;
  filter: blur(2px) brightness(0.5);
  position: fixed;
  top: -3%;
  left: 0%;
  width: 100%;
  height: 7px;
  background-image: url(./asd.png);
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
}
@media (max-width: 768px) {
  .screen .bg {
    background-size: contain;
  }
}
.screen .glow {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  animation: crt-glow 66.66ms infinite;
  background: radial-gradient(
    circle at center,
    #1bd459 0%,
    rgba(27, 212, 89, 0.88) 58%,
    rgba(21, 235, 92, 0.57) 80%,
    rgba(19, 94, 29, 0.27) 93%,
    rgba(10, 23, 12, 0) 100%
  );
}
